import React from 'react';
import { useSelector } from 'react-redux';
import { NavLink } from 'react-router-dom';

function UserInfo(props) {
    let { accountInfo } = useSelector(state => state.UserReducer);
    return (
        <div className="userInfo">
            <div className="userInfo__content">
                <h3>Thng tin c nhn</h3>
                <p>Ti khon : <span>{accountInfo.taiKhoan}</span></p>
                <p>H tn : <span>{accountInfo.hoTen}</span></p>
                <p>S in thoi : <span>{accountInfo.soDT}</span></p>
                <p>Email : <span>{accountInfo.email}</span></p>
            </div>
            <NavLink className="button" to="/user_update">
                <i class="fa fa-user-edit"></i>
                <span>Cp nht</span>
            </NavLink>
        </div>
    );
}

export default UserInfo;